.types
    &__description
        height: 100%
        background: $color-gray
        border-radius: 20px
    &__box-image
        height: 200px
    &__image
        height: 90%
        opacity: 0.6
        transition: 0.3s all
        cursor: pointer
        &:hover
            opacity: 0.9
    &__main-title
        font-size: 40px
